body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color:lightgoldenrodyellow;
  background-image: url(/bellyreact/static/media/zlybg.373f966e.PNG);
  background-size: cover;
  padding-bottom: 20px;
  height: 100%;
}
/* toto by bolo dobre keby fungovalo
.app>div{
  display: grid;
  height: 85vh;
}
*/
.landingPage{
  display: grid;
  min-height: 85vh;
  grid-template-rows: 50% 20% 15% 15%;
}



h2{
  text-align: center;
  font-size: 3em;
  margin: 0;
}

.searchPage{
  display: grid;
  min-height: 85vh;
  grid-template-rows: 30% auto 40px;
}

.searchGenre{
  display: grid;
  min-height: 85vh;
  grid-template-rows: 15% auto 40px;
}


.searchResult{
  display: grid;
  min-height: 85vh;
  height: auto;
  grid-template-rows: 250px auto 40px;
}

.ReactModalPortal h3{
  margin-top: 0;
}

